  <div v-if="lang===`pl`">
    <h4 class="fw-bold">Od Ankony do Bolonii</h4>
    <p>
      Bitwa o Monte Cassino stała się symbolem męstwa i wartości żołnierzy 2 Korpusu Polskiego, lecz w istocie była ona
      dopiero wstępem do długiego i krwawego szlaku Polaków przez całą Italię. Paradoksalnie też Monte Cassino
      przysłoniło znacznie donioślejsze zwycięstwa polskie we Włoszech – zdobycie Ankony z jej strategicznym portem, a
      następnie Bolonii.
      Zatknięcie biało-czerwonego sztandaru w ruinach opactwa benedyktyńskiego 18 maja 1944 roku, nie kończyło jeszcze
      bitwy pod Monte Cassino. Kiedy do sztabu generała Władysława Andersa zaczęły spływać zewsząd gratulacje, jego
      żołnierze wciąż walczyli o strategiczne wzgórza San Angelo, 575 i Pizzo Corno. W tym ostatnim punkcie, leżącym na
      prawym skrzydle polskiego natarcia, dopiero co przełamana linia Gustawa łączyła się z kolejną pozycją obronną
      Niemców – linią Hitlera.
      Obrona niemiecka była tu skomasowana i jeszcze bardziej zażarta. Polacy uchwycili kilka ważnych pozycji, ale byli
      za słabi, żeby z marszu przełamać linię Hitlera. Boleśnie przekonali się o tym w Piedimonte. To ufortyfikowane i
      obsadzone przez niemieckich spadochroniarzy miasteczko było jednym z kluczowych punktów linii Hitlera. Polscy
      dowódcy zamiast blokować Piedimonte do czasu przybycia posiłków z 8 Armii, zdecydowali się je zdobyć zagonem
      pancernym. Wyznaczony do tego zadania 6 Pułk Pancerny, bez wsparcia artylerii i osłony piechoty, stracił w walce
      aż 27 czołgów. Ostatecznie miasteczko zostało opanowane w nocy z 24 na 25 maja. W kilkanaście godzin później ułani
      z 15 Pułku Ułanów Poznańskich zdobyli Pizzo Corno, a wieczorem zatknęli biało-czerwony sztandar na Monte Cairo.
    </p>
    <h4 class="fw-bold">Triumf nad Adriatykiem</h4>
    <p>
      Polacy niewiele mieli czasu na odpoczynek po bojach o masyw Monte Cassino. Już 7 czerwca 3 Dywizja Strzelców
      Karpackich zluzowała Hindusów z 4 Dywizji Piechoty na północ od Oriony i rozpoczęła pościg za uciekającymi wzdłuż
      drogi nr 16 Niemcami. 18 czerwca karpatczycy zdobyli Pescarę, a do boju spod Riccia ruszyła 5 Kresowa Dywizja
      Piechoty. To właśnie jej przypadło główne uderzenie w kierunku Ankony, a co za tym idzie – ważnego dla
      zaopatrzenia alianckich armii portu. Tak pisał o tym generał Klemens Rudnicki, ówczesny zastępca dowódcy 5
      Kresowej Dywizji Piechoty generała Nikodema Sulika: „Czy jednak Polacy potrafią tego dokonać? Czy zdołają jednym
      zamachem iść w uporczywych bojach 200 kilometrów swymi skromnymi siłami? 200 długich kilometrów, najeżonych obroną
      przeciwnika […]. Zaiste, zadanie ponad miarę i chyba tylko na polską miarę!”.
      Generał Anders opracował plan bitwy o Ankonę w najdrobniejszych szczegółach, a jego żołnierze ruszyli do walki.
      Zdobyli kolejno Loreto, Recanati, Osimo, Castelfidardo i San Pietra. Z tego ostatniego wzgórza roztaczał się już
      widok na przedmieścia Ankony. Tak jak ustalono wcześniej, główne zadanie przypadło 5 Kresowej. Jej natarcie miało
      iść po linii Paterniano – Monte delia Crescia – Monte Tarto. W tym czasie 3 Karpacka miała wiązać siły niemieckie
      w pasie nadmorskim. Rankiem 17 lipca 1944 roku kresowiacy uderzyli i w zażartej walce zdobyli Paterniano i kolejne
      wyznaczone im cele. Zapadająca noc nie przerwała akcji, gdyż ułani poznańscy szerokim łukiem przedarli się nad
      rzekę Esino, pod Chiarvallę, a nawet dalej, aż pod Falconara. Kompletnie zaskoczyli tym Niemców, rozbijając między
      innymi wysłany specjalnie na pomoc Ankonie batalion „Wildflecken”.
      W ten sposób ostatecznie los Ankony i jej portu został przypieczętowany. Jak podkreślał generał Rudnicki: „Samo
      zajęcie miasta i portu Ankony było już rzeczą prostą. Po złamaniu centrum i prawego skrzydła nieprzyjaciela,
      Ankona wpadła w ręce Korpusu, jak dojrzałe jabłko, prawie niebroniona. Oddziały Karpackiej Dywizji, Ułanów
      Karpackich, spełniwszy znakomicie swoją rolę wiązania i mylenia przeciwnika, gdy tylko pierwsze rysy ukazały się
      na gmachu nieprzyjacielskim, ruszyły naprzód i ścigając odchodzącego przeciwnika wkroczyły do miasta”. Rozpoczęta
      dzień wcześniej bitwa, zakończona została 18 lipca w godzinach popołudniowych. Dzięki Polakom, alianci zyskali
      najlepszy port nad Adriatykiem, który umożliwił zaopatrywanie wojsk przełamujących kolejną pozycję obronną Niemców
      – linię Gotów. Sama bitwa przeszła do historii jako klasyczna zaplanowana i samodzielnie przeprowadzona przez 2
      Korpus Polski operacja.
    </p>
    <h4 class="fw-bold">Kolejne wzgórza, kolejne rzeki…</h4>
    <p>
      Tradycją frontu włoskiego stało się, że po kolejnej przełomowej bitwie, wojska nie miały czasu na wypoczynek i
      musiały ruszać od razu do walki. Tak też było po Ankonie. 2 Korpus od razu wrócił do walki. 3 Karpacka znowu
      ruszyła wzdłuż nadmorskiej drogi nr 16. Natomiast 5 Kresowa wsparta brytyjskimi czołgami opanowała linię rzeki
      Cezano i zdobyła miejscowość Monterado. To wszystko służyło temu, by pod osłoną polskiego korpusu, brytyjska 8
      Armia przegrupowała swe siły i uderzyła w kierunku Monte Lupone – Cattolica, rozrywając tym samym linię Gotów i
      wychodząc na równinę Ravenny i Bolonii. \
      19 sierpnia 1944 roku doszło do jednej z najcięższych bitew kampanii adriatyckiej z udziałem Polaków – o wzgórza
      nad rzeką Metauro. Do boju poszły tym razem wraz obie dywizje 2 Korpusu, wsparte czołgami 2 Brygady Pancernej.
      Generał Rudnicki wspominał: „I rozgorzała bitwa […], trwając pełne trzy doby, krwawa i zacięta głównie na odcinku
      Lwowskiej Brygady i rozstrzygnięta ostatecznie przez pułki pancerne. Przeciwnik wprowadził kilkadziesiąt dział
      szturmowych na gąsienicach i kilkanaście czołgów, w tym osławione Panthery. Trzy dni i trzy noce trwały zmagania
      pełne kryzysów, niejasnych sytuacji, nieludzkich wysiłków”.
      Po przekroczeniu Metauro, kresowiacy zdobyli miejscowości Lucrecia Borgia, Monte Forche i dotarli do następnej
      rzeki – Arzilla. Ale niemiecka obrona była coraz słabsza, gdyż w sukurs 2 Korpusowi przybyli Kanadyjczycy. Oto
      całym frontem szło natarcie 8 Armii i Niemcy nie mieli już oparcia w linii Gotów. 28 sierpnia 5 Kresowa wchodzi
      nad rzekę Foglia, tym samym osiągając linię Gotów. Artylerzysta Edward Szczepanik wspominał: „By nie dać Szwabom
      wytchnienia, niezmordowana Grupa Artylerii rozpoczęła robić w mig to, co łagodnie było również nazwane
      «zmiękczaniem». W istocie polegało ono na nieustannym okładaniem ogniem artylerii umocnień linii Gotów przez dwa
      dni […], co w sumie wystarczyło Niemcom do szybkiego zaprzestania snów o obronie linii Gotów na odcinku polskiego
      Korpusu. Zrobiliśmy swoje – można było jechać na odpoczynek”. 5 września 2 Korpus wycofany został do odwodu 8
      Armii i ześrodkowany na północ od Ankony. W kampanii adriatyckiej polski korpus stracił 1358 żołnierzy. Większość
      z nich spoczywa na cmentarzu polskim w Loreto.
    </p>
    <h4 class="fw-bold">Czerwone dachy Bolonii</h4>
    <p>
      W kwietniu 1945 roku polscy żołnierze ruszyli do swej ostatniej wielkiej bitwy na froncie włoskim. Tym razem celem
      polskich żołnierzy było Czerwone Miasto – zwana tak od koloru swych dachów Bolonia. 2 Korpus, uzupełniany
      nieprzerwanie Polakami wcielonymi do armii niemieckiej, którzy z niej uciekali albo po dostaniu się do niewoli,
      prosili o wcielenie do polskiego wojska, w ostatnim roku wojny liczył 90 tysięcy żołnierzy. Jak sprawna była to
      bojowo formacja, świadczy właśnie kampania bolońska.
      W ostatniej fazie bitwy, 20 kwietnia, Polacy przełamali obronę doborowych wojsk niemieckich – spadochronowych i
      pancernych – na linii rzeki Gaiana i Idice. Porucznik Bohdan Tymieniecki tak zapamiętał atak polskich komandosów
      nad Gaianą: „Huk targnął ciszą poranka. Poszła pierwsza salwa. Przed nami o sześćset metrów wyrosła równa ściana
      czarnych wybuchów. [...] Podniosło się Commando. [...] Z prawej, z tyłu, podchodzi piechota. Z lewej wychodzą zza
      drzew czołgi czwartego pułku, idą jeden za drugim, prosto na most. [...] W górze smuga i czerwona rakieta. To
      Commando dawało mi znać, że przeszli rzekę”. Ten cytat dobrze oddaje tempo polskiego natarcia na Bolonię. Już
      następnego dnia, 21 kwietnia, kilka minut po godzinie szóstej rano, żołnierze 3 Brygady Strzelców Karpackich
      zatknęli nad miastem biało-czerwony sztandar. Jakież było zdziwienie Amerykanów z 5 Armii, którzy pojawili się w
      Bolonii dwie godziny później. W porównaniu z Polakami mieli do miasta bliżej o 30 kilometrów!
      W kampanii bolońskiej 2 Korpus przetrzebił znacznie elitarne formacje niemieckie – 26 Dywizję Pancerną, 4 Dywizję
      Strzelców Spadochronowych i starych znajomych spod Monte Cassino z 1 Dywizji Strzelców Spadochronowych. To
      przyczyniło się do decyzji niemieckiego dowództwa o rychłej kapitulacji jego wojsk we Włoszech.
    </p>
