    <div v-else-if="lang===`en`">
      <h4 class="fw-bold">From Ancona to Bologna</h4>
    <p>
      The Battle of Monte Cassino became a symbol of the bravery and value of the soldiers of the 2nd Polish Corps, but in fact it was only an introduction to the long and bloody route of Poles throughout Italy. Paradoxically, Monte Cassino was also overshadowed by the much more significant Polish victories in Italy - the capture of Ancona with its strategic port, and then Bologna. The placing of the white and red banner in the ruins of the Benedictine abbey on May 18, 1944 did not end the Battle of Monte Cassino. When congratulations began to flow to General Władysław Anders's staff, his soldiers were still fighting for the strategic hills of San Angelo, 575 and Pizzo Corno. At this last point, lying on the right flank of the Polish assault, Gustav's just broken line was connected with another defensive position of the Germans - Hitler's line. The German defense was concentrated here and even more fierce. The Poles captured several important positions, but were too weak to break Hitler's line on the move. They found this out painfully in Piedimonte. This fortified town, manned by German paratroopers, was one of the key points of Hitler's line. Polish commanders, instead of blocking Piedimonte until the arrival of reinforcements from the 8th Army, decided to capture them with armored troops. The 6th Panzer Regiment assigned for this task, without the support of artillery and infantry protection, lost as many as 27 tanks in combat. Ultimately, the town was taken over the night of May 24-25. Several hours later, the lancers of the 15th Poznań Uhlans' Regiment captured Pizzo Corno, and in the evening they planted the white and red banner on Monte Cairo.


    </p>
    <h4 class="fw-bold">Triumph over the Adriatic Sea</h4>
    <p>
      Poles had little time to rest after the battles for the Monte Cassino massif. On June 7, the 3rd Carpathian Rifle Division relieved the Indians of the 4th Infantry Division north of Oriona and began chasing the Germans fleeing along Route 16. On June 18, the Carpathians captured Pescara, and the 5th Kresowa Infantry Division began fighting from Riccia. It was her who struck the main attack towards Ancona, and thus - an important port for the supply of the Allied armies. This is what General Klemens Rudnicki, the then deputy commander of the 5th Kresowa Infantry Division of general Nikodem Sulik, wrote about it: “But can Poles do it? Will they manage to walk 200 kilometers in persistent battles with their modest forces in one go? 200 long kilometers, bristling with the enemy's defense [...]. Indeed, a task beyond measure and probably only for the Polish measure! ”. General Anders devised a plan for the Battle of Ancona down to the last detail, and his troops went into battle. They conquered Loreto, Recanati, Osimo, Castelfidardo and San Pietra in turn. From this last hill there was a view of the outskirts of Ancona. As previously established, the main task fell to the 5th Kresowa. Its attack was to follow the line Paterniano - Monte delia Crescia - Monte Tarto. At that time, the 3 Karpacka was to bind the German forces in the coastal zone. On the morning of July 17, 1944, the borderlanders struck and in a fierce struggle captured Paterniano and the next targets set for them. The falling night did not interrupt the action, as the Poznań uhlans in a wide arc broke through the Esino River, under Chiarvalla, and even further, as far as Falconara. They completely surprised the Germans, destroying, among other things, the "Wildflecken" battalion specially sent to help Ancona. Thus, the fate of Ancona and its port was finally sealed. As General Rudnicki emphasized: “The very seizure of the city and port of Ancona was already a simple matter. After breaking the enemy's center and right wing, Ancona fell into the hands of the Corps like a ripe apple, almost undefended. The troops of the Carpathian Division, the Carpathian Uhlans, perfectly fulfilling their role of binding and confusing the opponent, as soon as the first scratches appeared on the enemy building, they moved forward and, pursuing the departing enemy, entered the city ”. The battle started the day before and ended on July 18 in the afternoon. Thanks to the Poles, the Allies gained the best port on the Adriatic, which made it possible to supply the troops breaking another defensive position of the Germans - the Goth line. The battle itself went down in history as a classic operation planned and carried out independently by the 2nd Polish Corps.


    </p>
    <h4 class="fw-bold">More hills, more rivers ...</h4>
    <p>
      It became a tradition of the Italian front that after another groundbreaking battle, the troops did not have time to rest and had to go straight to the fight. This was also the case after Ancona. The 2nd Corps immediately resumed the fight. 3 Karpacka again moved along the coastal road No. 16. Whereas 5 Kresowa, supported by British tanks, conquered the Cezano river line and captured the town of Monterado. All this served to ensure that, under the cover of the Polish corps, the British 8th Army regrouped its forces and attacked Monte Lupone - Cattolica, thereby breaking the Goth line and emerging into the plain of Ravenna and Bologna. \ On August 19, 1944, one of the heaviest battles of the Adriatic campaign involving Poles took place - for the hills above the Metauro River. This time both divisions of the 2nd Corps, supported by tanks of the 2nd Armored Brigade, went into battle this time. General Rudnicki recalled: “And the battle started […], lasting three full days, bloody and fierce, mainly in the section of the Lviv Brigade, and finally decided by armored regiments. The enemy introduced several dozen assault guns on caterpillars and a dozen or so tanks, including the notorious Panthers. Three days and three nights were struggling full of crises, unclear situations, and inhuman efforts. " After crossing Metauro, the borderlanders captured the towns of Lucrecia Borgia, Monte Forche and reached the next river - Arzilla. But the German defense was getting weaker and weaker as Canadians came to assist the 2nd Corps. The 8th Army was attacking with the entire front and the Germans no longer had a Goth line backing. On August 28, 5 Kresowa enters the Foglia River, thus reaching the Goth line. Artillery artist Edward Szczepanik recalled: “In order not to give the Swabians a break, the tireless Artillery Group began to do what was gently also called" softening ". In fact, it consisted in the continuous firing of artillery fire on the fortifications of the Goth line for two days [...], which was enough for the Germans to quickly stop their dreams of defending the Goth line in the Polish section of the Corps. We did our job - it was possible to go to rest ”. On September 5, the 2nd Corps was withdrawn to the 8th Army reserve and centered north of Ancona. In the Adriatic campaign, the Polish corps lost 1,358 soldiers. Most of them are buried in the Polish cemetery in Loreto.
    </p>
    <h4 class="fw-bold">Bologna's red roofs</h4>
    <p>
      In April 1945, Polish soldiers set off for their last great battle on the Italian front. This time the target of the Polish soldiers was the Red City - Bologna, so called because of the color of its roofs. The 2nd Corps, continuously supplemented by Poles conscripted into the German army, who either escaped from it or after being taken prisoner, asked to be drafted into the Polish army, in the last year of the war it numbered 90,000 soldiers. How efficient this formation was, is the Bologna campaign. In the last phase of the battle, on April 20, the Poles broke the defense of selected German troops - parachute and armored - on the line of the Gaiana and Idice rivers. This is how Lieutenant Bohdan Tymieniecki remembered the attack of Polish commandos over Gaiana: “The boom struck the morning silence. The first salvo went. Six hundred meters ahead of us rose an even wall of black eruptions. [...] Commando rose. [...] On the right, at the back, there is an infantry. From the left, the tanks of the 4th regiment come out from behind the trees, they follow one another straight to the bridge. [...] Up a streak and a red rocket. It was Commando that let me know that they had crossed the river. This quote accurately reflects the pace of the Polish attack on Bologna. The very next day, on April 21, a few minutes after six in the morning, the soldiers of the 3rd Carpathian Rifle Brigade planted a white and red banner over the city. Imagine the surprise of the Americans of the 5th Army who arrived in Bologna two hours later. Compared to Poles, they were 30 kilometers closer to the city! In the Bologna campaign, the 2nd Corps considerably destroyed the elite German formations - the 26th Panzer Division, the 4th Parachute Division and old acquaintances from the 1st Parachute Rifle Division near Monte Cassino. This contributed to the decision of the German command to surrender its troops imminently in Italy.



    </p>
