    <div v-else-if="lang===`it`">
      <h4 class="fw-bold">Da Ancona a Bologna</h4>
    <p>
      La battaglia di Montecassino divenne un simbolo del coraggio e del valore dei soldati del 2 ° Corpo d'Armata Polacco, ma in realtà era solo un'introduzione alla lunga e sanguinosa rotta dei polacchi in tutta Italia. Paradossalmente, Monte Cassino fu anche offuscato dalle vittorie polacche molto più significative in Italia: la conquista di Ancona con il suo porto strategico, e poi di Bologna. La deposizione dello stendardo bianco e rosso tra le rovine dell'abbazia benedettina il 18 maggio 1944 non pose fine alla battaglia di Monte Cassino. Quando le congratulazioni iniziarono a giungere allo staff del generale Władysław Anders, i suoi soldati stavano ancora combattendo per le colline strategiche di San Angelo, 575 e Pizzo Corno. In questo ultimo punto, che giace sul fianco destro dell'assalto polacco, la linea appena interrotta di Gustav era collegata con un'altra posizione difensiva dei tedeschi: la linea di Hitler. La difesa tedesca era concentrata qui e ancora più agguerrita. I polacchi conquistarono diverse posizioni importanti, ma erano troppo deboli per rompere la linea di Hitler in movimento. L'hanno scoperto dolorosamente a Piedimonte. Questa città fortificata, presidiata da paracadutisti tedeschi, era uno dei punti chiave della linea di Hitler. I comandanti polacchi, invece di bloccare Piedimonte fino all'arrivo dei rinforzi dell'VIII Armata, decisero di catturarli con truppe corazzate. Il 6 ° Reggimento Panzer assegnato per questo compito, senza il supporto di artiglieria e protezione di fanteria, perse ben 27 carri armati in combattimento. Alla fine, la città è stata conquistata la notte tra il 24 e il 25 maggio. Diverse ore dopo, i lancieri del 15 ° reggimento degli ulani di Poznań catturarono il Pizzo Corno e la sera piantarono lo stendardo bianco e rosso sul Monte Cairo.
    </p>
    <h4 class="fw-bold">Trionfo sul mare Adriatico</h4>
    <p>
      I polacchi hanno avuto poco tempo per riposarsi dopo le battaglie per il massiccio di Monte Cassino. Il 7 giugno, la 3a Divisione Fucilieri dei Carpazi sollevò gli indiani della 4a Divisione di fanteria a nord di Oriona e iniziò a inseguire i tedeschi in fuga lungo la Rotta 16. Il 18 giugno, i Carpazi catturarono Pescara e la 5a Divisione di fanteria di Kresowa iniziò a combattere da Riccia. Fu lei a sferrare l'attacco principale verso Ancona, e quindi - un importante porto per l'approvvigionamento degli eserciti alleati. Questo è ciò che scrisse al riguardo il generale Klemens Rudnicki, l'allora vice comandante della 5a divisione di fanteria di Kresowa del generale Nikodem Sulik: “Ma possono farlo i polacchi? Riusciranno a percorrere 200 chilometri in battaglie persistenti con le loro modeste forze in una volta sola? 200 lunghi chilometri, irti della difesa del nemico [...]. Anzi, un compito oltre misura e probabilmente solo per la misura polacca! ". Il generale Anders escogitò un piano per la battaglia di Ancona fin nei minimi dettagli e le sue truppe entrarono in battaglia. A loro volta conquistarono Loreto, Recanati, Osimo, Castelfidardo e San Pietra. Da quest'ultima collina si vedeva la periferia di Ancona. Come stabilito in precedenza, il compito principale spettava al 5 ° Kresowa. Il suo attacco doveva seguire la linea Paterniano - Monte delia Crescia - Monte Tarto. A quel tempo, il 3 Karpacka doveva legare le forze tedesche nella zona costiera. La mattina del 17 luglio 1944, i borderlander attaccarono e in una feroce lotta catturarono Paterniano e gli obiettivi successivi fissati per loro. La notte calante non interruppe l'azione, poiché gli ulani di Poznań in un ampio arco sfondarono il fiume Esino, sotto Chiarvalla, e ancora oltre, fino a Falconara. Sorpresero completamente i tedeschi, distruggendo, tra l'altro, il battaglione "Wildflecken" inviato appositamente per aiutare Ancona. Così, il destino di Ancona e del suo porto fu finalmente segnato. Come ha sottolineato il generale Rudnicki: “Già il sequestro della città e del porto di Ancona era cosa semplice. Dopo aver spezzato il centro e l'ala destra del nemico, Ancona cadde nelle mani del Corpo come una mela matura, quasi indifesa. Le truppe della Divisione dei Carpazi, gli Ulani dei Carpazi, assolvendo perfettamente il loro ruolo di legare e confondere l'avversario, non appena apparvero i primi graffi sull'edificio nemico, si mossero in avanti e, inseguendo il nemico in partenza, entrarono in città ”. La battaglia è iniziata il giorno prima e si è conclusa il 18 luglio nel pomeriggio. Grazie ai polacchi, gli alleati ottennero il miglior porto dell'Adriatico, il che rese possibile rifornire le truppe rompendo un'altra posizione difensiva dei tedeschi: la linea dei Goti. La battaglia stessa è passata alla storia come un'operazione classica pianificata ed eseguita in modo indipendente dal 2 ° Corpo d'Armata Polacco.




    </p>
    <h4 class="fw-bold">Più colline, più fiumi ...</h4>
    <p>
      Divenne una tradizione del fronte italiano che, dopo un'altra battaglia rivoluzionaria, le truppe non avessero tempo di riposare e dovessero andare direttamente al combattimento. Così è stato anche dopo Ancona. Il 2 ° Corpo riprese immediatamente il combattimento. 3 Karpacka si spostò di nuovo lungo la strada costiera n. 16. Mentre 5 Kresowa, sostenuto da carri armati britannici, conquistò la linea del fiume Cezano e conquistò la città di Monterado. Tutto ciò servì a far sì che, sotto la copertura del corpo polacco, l'8a Armata britannica riorganizzasse le sue forze e attaccasse il Monte Lupone - Cattolica, rompendo così la linea gotica ed emergendo nella pianura di Ravenna e Bologna. \ Il 19 agosto 1944 si svolse una delle più pesanti battaglie della campagna adriatica che coinvolse i polacchi - per le colline sopra il fiume Metauro. Questa volta entrambe le divisioni del 2 ° Corpo, supportate dai carri armati della 2 ° Brigata Corazzata, entrarono in battaglia questa volta. Il generale Rudnicki ha ricordato: “E la battaglia iniziò […], durando tre giorni interi, sanguinosi e feroci, principalmente nella sezione della Brigata di Leopoli, e infine decisa dai reggimenti corazzati. Il nemico ha introdotto diverse dozzine di cannoni d'assalto sui bruchi e una dozzina circa di carri armati, inclusi i famigerati Panthers. Tre giorni e tre notti sono stati di lotta piena di crisi, situazioni poco chiare e sforzi disumani. "Dopo aver attraversato il Metauro, le frontiere conquistarono le città di Lucrecia Borgia, Monte Forche e raggiunsero il fiume successivo - Arzilla. Ma la difesa tedesca si stava indebolendo sempre di più man mano che i canadesi venivano ad assistere il 2 ° Corpo. L'8 ° Armata stava attaccando con l'intera il fronte ei tedeschi non avevano più un appoggio di linea gotica. Il 28 agosto, 5 Kresowa entra nel fiume Foglia, raggiungendo così la linea gota. L'artigliere Edward Szczepanik ricorda: "Per non dare tregua agli Svevi, l'instancabile Gruppo di artiglieria cominciò a fare quello che con dolcezza veniva chiamato anche "ammorbidimento", che consisteva infatti nel fuoco continuo di artiglieria sulle fortificazioni della linea gotica per due giorni [...], che bastò ai tedeschi per fermare rapidamente la loro sogna di difendere la linea gotica nella sezione polacca del Corpo. Abbiamo fatto il nostro lavoro, era possibile andare a riposare. "Il 5 settembre il 2 ° Corpo è stato ritirato nella riserva dell'8 ° Armata e centrato a nord di Ancona. Campagna adriatica n, il corpo polacco ha perso 1.358 soldati. La maggior parte di loro sono sepolti nel cimitero polacco di Loreto.
    </p>
    <h4 class="fw-bold">I tetti rossi di Bologna</h4>
    <p>
      Nell'aprile 1945, i soldati polacchi partirono per la loro ultima grande battaglia sul fronte italiano. Questa volta il bersaglio dei soldati polacchi era la Città Rossa - Bologna, così chiamata per il colore dei suoi tetti. Il 2 ° Corpo, continuamente integrato da polacchi arruolati nell'esercito tedesco, che o fuggì da esso o dopo essere stato fatto prigioniero, chiese di essere arruolato nell'esercito polacco, nell'ultimo anno di guerra contava 90.000 soldati. Quanto è stata efficiente questa formazione, è la campagna di Bologna. Nell'ultima fase della battaglia, il 20 aprile, i polacchi ruppero la difesa di truppe tedesche selezionate - paracadutisti e corazzate - sulla linea dei fiumi Gaiana e Idice. Così il tenente Bohdan Tymieniecki ha ricordato l'attacco dei commando polacchi su Gaiana: “Il boom ha colpito il silenzio mattutino. La prima salva è andata. Seicento metri davanti a noi si ergeva un muro uniforme di eruzioni nere. [...] Commando si alzò. [...] A destra, in fondo, c'è una fanteria. Da sinistra, i carri armati del 4 ° reggimento escono da dietro gli alberi, si susseguono dritti fino al ponte. [...] Alza una striscia e un razzo rosso. Fu Commando a farmi sapere che avevano attraversato il fiume. Questa citazione rispecchia fedelmente il ritmo dell'attacco polacco al Bologna. Il giorno successivo, il 21 aprile, pochi minuti dopo le sei del mattino, i soldati della 3a Brigata Fucilieri dei Carpazi hanno piantato uno striscione bianco e rosso sulla città. Immaginate la sorpresa degli americani della 5a Armata arrivati a Bologna due ore dopo. Rispetto ai polacchi, erano 30 chilometri più vicini alla città! Nella campagna di Bologna, il 2 ° Corpo distrusse considerevolmente le formazioni tedesche d'élite - la 26a Divisione Panzer, la 4a Divisione Paracadutisti e vecchie conoscenze della 1a Divisione Paracadutisti vicino a Monte Cassino. Ciò contribuì alla decisione del comando tedesco di consegnare imminentemente le sue truppe in Italia.



    </p>
